/* Importación de tipografías recomendadas */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Source+Sans+Pro:wght@300;400&family=Lato:wght@300;400&display=swap');

/* Estilos globales */
body {
    font-family: 'Source Sans Pro', sans-serif;
    background-color: #F5F1E8; /* Beige claro */
    color: #4A3E30; /* Marrón oscuro */
    margin: 0;
    padding: 0;
}

/* Header */
header {
    text-align: center;
    padding: 20px;
}

.profile-img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

/* Contenedor de cards: centra las secciones */
.card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    padding: 20px;
}

/* Estilo para cada card (secciones) */
.card {
    background-color: #ffffff; /* Fondo blanco */
    border-radius: 15px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    padding: 40px;
    width: 100%;
    max-width: 1024px;
    box-sizing: border-box;
}

/* Títulos de sección */
h2 {
    font-family: 'Playfair Display', serif;
    text-align: center;
    color: #6B5B45; /* Marrón medio */
}

/* Sección: Presentación */
#presentation p {
    text-align: justify;
}

/* Sección: Proyectos */
.projects-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    justify-items: center;
}

.project-card {
    background-color: #E8E0D0; /* Beige cálido */
    border: 1px solid #A58D65; /* Beige tostado */
    border-radius: 8px;
    padding: 20px;
    width: 100%;
    max-width: 300px;
    text-align: center;
}

.project-card img {
    width: 100%;
    height: auto;
    border-radius: 4px;
}

/* Sección: Conocimientos */
.skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.skill {
    text-align: center;
}

.skill img {
    width: 50px;
    height: 50px;
}

/* Certificaciones */
.certifications {
    text-align: center;
    margin-top: 20px;
}

.certifications ul {
    list-style: none;
    padding: 0;
}

/* Sección: Contacto */
.contact-buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
}

.button {
    background-color: #C2A878; /* Beige dorado */
    color: #F5F1E8;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 5px;
    font-family: 'Lato', sans-serif;
}

.social-media {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

.social-media img {
    width: 40px;
    height: 40px;
}

/* Ajustes responsivos */
@media (max-width: 768px) {
  .card {
    padding: 20px;
  }
  .profile-img {
    width: 100px;
    height: 100px;
  }
}
